export default{
  data () {
    return {
      nodes: ''
    }
  },
  onLoad(opt) {
    console.log(opt.id)
    this.getDetail(opt.id)
  },
  methods: {
    getDetail(id) {
      let that= this,
        params= {
          url: that.$api.getAgreeContentById,
          data: {
            category_id: id
          }
        }
      that.$httpRequest(params).then(res => {
        that.nodes= res.data.content.content
      })
    }
  }
}
